<template>
  <div class="scene" ref="scene">
    <div id="three-scene-canvas"></div>
    <div class="svg-editor" ref="svgEditor" v-show="isEditorEnabled">
      <div v-if="!isSvgUploaded">
        <button class="upload-svg-button" v-on:click="onPickFile">
          Upload SVG ↑
        </button>
        <input
          class="fileInput"
          type="file"
          ref="fileInput"
          accept=".svg"
          v-on:change="onFilePicked"
        />
      </div>

      <div class="svg-container" v-show="isSvgUploaded">
        <svg
          ref="svgContainer"
          id="stage"
          width="510"
          height="510"
          viewBox="0 0 510 510"
        ></svg>
      </div>
    </div>

    <button
      v-if="isEditorEnabled"
      class="finsih-edit-button button default green"
      v-on:click="editFinished"
    >
      finish Edit
    </button>

    <button
      v-if="isEditorEnabled && isSvgUploaded"
      class="clear button round red"
      v-on:click="clearSVG"
    >
      X
    </button>

    <div v-if="!isEditorEnabled" class="overlay">
      <button
        v-on:click="startEditor"
        class="start-svg-editor-button button default green"
      >
        Edit
      </button>
      <button
        v-if="isObjDownloadReady"
        v-on:click="downloadObj"
        class="download-button button default green"
      >
        Download ↓
      </button>
    </div>
  </div>
</template>

<script>
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { OBJLoader, MTLLoader } from "three-obj-mtl-loader";
import { SVGLoader } from "threecustomsvgloader";
let FileSaver = require("file-saver");
//import { BufferGeometryUtils } from "three-buffer-geometry-utils";
let OBJExporter = require("three-obj-exporter");
import * as TWEEN from "@tweenjs/tween.js";
//let Draggable = require("draggable");

export default {
  name: "Scene",
  data() {
    return {
      sceneCanvas: null,
      scene: null,
      camera: null,
      renderer: null,
      controls: null,
      buttonCapMesh: null,
      isEditorEnabled: false,
      isSvgUploaded: false,
      svgHover: false,
      svgTranslation: { x: 0, y: 0, scale: 1 },
      svgScale: 1,
      svgFilename: "",
      svgDraggable: null,
      maxBounding: {
        x: 600,
        y: 600,
      },
      svgExtruded: null,
      isObjDownloadReady: false,
      objDownloadData: null,
    };
  },
  mounted() {
    /* **************
       BASIC SETUP
    ************** */
    const showGrid = false;
    const showHelper = false;

    this.sceneCanvas = document.getElementById("three-scene-canvas");
    this.scene = new THREE.Scene();
    this.scene.background = new THREE.Color(0x000000);
    this.camera = new THREE.PerspectiveCamera(
      55,
      window.innerWidth / window.innerHeight,
      1,
      10000
    );
    this.camera.position.set(0, 150, 220);
    this.camera.lookAt(0, 0, 0);

    if (showGrid) {
      let gridHelper = new THREE.GridHelper(1000, 20);
      this.scene.add(gridHelper);
    }

    this.renderer = new THREE.WebGLRenderer({
      antialias: true,
      powerPreference: "high-performance",
    });
    this.renderer.outputEncoding = THREE.sRGBEncoding;

    this.controls = new OrbitControls(this.camera, this.renderer.domElement);
    this.controls.enablePan = false;
    this.controls.addEventListener("change", this.animateThreeJs);

    this.renderer.setSize(
      this.sceneCanvas.offsetWidth,
      this.sceneCanvas.offsetHeight
    );
    this.renderer.setClearColor("#212121");
    this.renderer.shadowMap.enabled = true;
    this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;
    this.renderer.shadowMapSoft = true;
    this.renderer.shadowMap.autoUpdate = false;
    this.renderer.shadowMap.needsUpdate = true;
    this.sceneCanvas.append(this.renderer.domElement);

    // lighting
    let ambientLight = new THREE.AmbientLight(0x606060);
    this.scene.add(ambientLight);

    let light = new THREE.PointLight(0xfc831d, 1.5, 100);
    light.position.set(0, 80, 0);
    light.castShadow = true;
    light.shadow.radius = 2;
    light.shadow.mapSize.width = 2048;
    light.shadow.mapSize.height = 2048;
    this.scene.add(light);

    let greenLedLight = new THREE.PointLight(0x00ffae, 1, 200);
    greenLedLight.position.set(150, 10, 100);
    greenLedLight.castShadow = true;
    greenLedLight.shadow.radius = 2;
    greenLedLight.shadow.mapSize.width = 2048;
    greenLedLight.shadow.mapSize.height = 2048;
    this.scene.add(greenLedLight);
    if (showHelper) {
      let helper = new THREE.PointLightHelper(greenLedLight);
      this.scene.add(helper);
    }

    let redLedLight = new THREE.PointLight(0xff0000, 1, 200);
    redLedLight.position.set(-150, 10, 100);
    redLedLight.castShadow = true;
    redLedLight.shadow.radius = 2;
    redLedLight.shadow.mapSize.width = 2048;
    redLedLight.shadow.mapSize.height = 2048;
    this.scene.add(redLedLight);

    if (showHelper) {
      let helper = new THREE.PointLightHelper(redLedLight);
      this.scene.add(helper);
    }

    // Adding a cube
    /*  let geometry = new THREE.BoxGeometry();
    let material = new THREE.MeshPhysicalMaterial({ color: 0x00ff00 });
    let cube = new THREE.Mesh(geometry, material);
    this.scene.add(cube); */

    let that = this;

    const mtlLoader = new MTLLoader();
    mtlLoader.load("/models/assemblyV4.mtl", (materials) => {
      materials.preload();

      const objLoader = new OBJLoader();
      objLoader.setMaterials(materials);
      objLoader.load(
        "/models/assemblyV4.obj",
        function(obj) {
          obj.position.x = -70;
          obj.position.y = -27.6;
          obj.position.z = 70;

          that.buttonCapMesh = obj.clone();

          that.scene.add(obj);

          that.animateThreeJs();
        },
        (xhr) => {
          console.log((xhr.loaded / xhr.total) * 100 + "% loaded");
        }
      );
    });

    const loader = new OBJLoader();

    loader.load(
      "/models/ButtonCap.obj",
      function(obj) {
        obj.position.x = -91;
        obj.position.y = -9.9;
        obj.position.z = 30;
        obj.rotateX(Math.PI / 2);

        that.buttonCapMesh = obj.clone();

        that.scene.add(obj);

        //that.loadSVG();

        that.animateThreeJs();
      },
      function(xhr) {
        console.log((xhr.loaded / xhr.total) * 100 + "% loaded");
      },
      function(error) {
        console.log(error);
        console.log("An error happened");
      }
    );

    this.animateThreeJs();

    window.addEventListener("resize", () => {
      this.resizeCanvas();
    });
  },
  methods: {
    animateThreeJs() {
      this.renderer.render(this.scene, this.camera);
      this.renderer.shadowMap.needsUpdate = true;
    },

    resizeCanvas() {
      this.camera.aspect = window.innerWidth / window.innerHeight;
      this.camera.updateProjectionMatrix();
      this.renderer.setPixelRatio(window.devicePixelRatio);
      this.renderer.setSize(window.innerWidth, window.innerHeight);
      this.animateThreeJs();
    },

    loadSVG(dataUrl) {
      const loader = new SVGLoader();
      let that = this;

      let url = "/svg/phone-call.svg";

      if (dataUrl != undefined) {
        url = dataUrl;
      }

      loader.load(
        // resource URL
        url,
        // called when the resource is loaded
        function(svgData) {
          const svgGroup = new THREE.Group();

          const scalingFactor = 0.0372;
          const adjScalingFactor = scalingFactor / that.svgScale;

          svgGroup.scale.y = -1 * adjScalingFactor;
          svgGroup.scale.x = adjScalingFactor;
          svgGroup.scale.z = scalingFactor;

          const yOffset = 9.5 / adjScalingFactor - that.svgTranslation.y;
          const xOffset = -9.5 / adjScalingFactor + that.svgTranslation.x;
          const zOffset = -118;

          svgGroup.rotateX(Math.PI / 2);

          // let height = 10;

          let colors = [];

          svgData.paths.forEach((path) => {
            let c = that.colorToHex(path.color);
            if (!colors.includes(c)) {
              colors.push(c);
            }
          });

          svgData.paths.forEach((path) => {
            const shapes = path.toShapes(false);

            let c = that.colorToHex(path.color);

            let index = colors.indexOf(c);
            // Each path has array of shapes
            shapes.forEach((shape) => {
              // Finally we can take each shape and extrude it
              const mesh = that.createMesh(
                new THREE.ExtrudeGeometry(shape, {
                  depth: index * 20 + 20,
                  bevelEnabled: false,
                }),
                c
              );
              //   geometry.scale(scalingFactor, scalingFactor, scalingFactor)
              //  geometry.translate(xOffset, yOffset, zOffset)

              svgGroup.add(mesh);
            });
          });

          // Get group's size
          const box = new THREE.Box3().setFromObject(svgGroup);
          const size = new THREE.Vector3();
          box.getSize(size);

          // Offset all of group's elements, to center them
          svgGroup.children.forEach((item) => {
            //item.geometry.scale(scalingFactor, scalingFactor, scalingFactor)
            //item.geometry.translate(xOffset, yOffset, zOffset)
            item.position.x = xOffset;
            item.position.y = yOffset;
            item.position.z = zOffset;
            item.updateWorldMatrix(true, true);
          });

          //svgGroup.scale(scalingFactor, scalingFactor, scalingFactor)

          // let mergedButton = mergeMesh(svgGroup.clone(), buttonCapMesh)

          // Finally we add svg group to the scene

          //let svgGroupClone = svgGroup.clone()

          var exporter = new OBJExporter();
          setTimeout(() => {
            that.objDownloadData = exporter.parse(
              that.mergeExtrusion(svgGroup.clone())
            );
            that.isObjDownloadReady = true;
          }, 1000);

          that.scene.add(svgGroup);
          that.svgExtruded = svgGroup;

          //  svgGroupClone.attach(buttonCapMesh)
          //  var exporter = new OBJExporter()
          //  console.log(exporter.parse(svgGroupClone))
        },
        // called when loading is in progresses
        function(xhr) {
          console.log((xhr.loaded / xhr.total) * 100 + "% loaded");
        },
        // called when loading has errors
        function(error) {
          console.error(error);
          console.log("An error happened");
        }
      );
    },

    mergeExtrusion(extrusion) {
      let buttonMesh = this.buttonCapMesh;

      extrusion.updateMatrix(true);
      buttonMesh.updateMatrix(true);

      let extrusionGeometries = [];
      let buttonGeometry;

      extrusion.traverse(function(child) {
        if (child.geometry !== undefined) {
          extrusionGeometries.push(child.geometry);
        }
      });

      buttonMesh.traverse(function(child) {
        if (child.geometry !== undefined) {
          buttonGeometry = new THREE.Geometry().fromBufferGeometry(
            child.geometry
          );
        }
      });

      for (let i = 0; i < extrusionGeometries.length; i++) {
        extrusionGeometries[i] = this.assignUVs(extrusionGeometries[i]);
      }

      buttonGeometry = this.assignUVs(buttonGeometry);

      var geom = new THREE.Geometry();

      var matrix1 = new THREE.Matrix4();

      let scalingFactor = 0.0392 / this.svgScale;
      matrix1.multiply(
        new THREE.Matrix4().makeScale(scalingFactor, scalingFactor, 0.0372)
      );
      matrix1.multiply(
        new THREE.Matrix4().makeTranslation(
          -10 / scalingFactor,
          22.5 / scalingFactor,
          -93
        )
      );
      matrix1.multiply(new THREE.Matrix4().makeRotationX(Math.PI));

      var matrix2 = new THREE.Matrix4();
      matrix2.multiply(new THREE.Matrix4().makeTranslation(-91, 42.5, 11));

      for (let g of extrusionGeometries) {
        geom.merge(g, matrix1);
      }
      geom.merge(buttonGeometry, matrix2);
      geom.mergeVertices();
      geom.groupsNeedUpdate = true; // optional

      const material = new THREE.MeshNormalMaterial();
      return new THREE.Mesh(geom, material);
    },

    assignUVs(geometry) {
      geometry.computeFaceNormals();
      geometry.mergeVertices();
      geometry.faceVertexUvs[0] = [];
      geometry.faces.forEach((face) => {
        const components = ["x", "y", "z"].sort((a, b) => {
          return Math.abs(face.normal[a]) > Math.abs(face.normal[b]);
        });
        const v1 = geometry.vertices[face.a];
        const v2 = geometry.vertices[face.b];
        const v3 = geometry.vertices[face.c];
        geometry.faceVertexUvs[0].push([
          new THREE.Vector2(v1[components[0]], v1[components[1]]),
          new THREE.Vector2(v2[components[0]], v2[components[1]]),
          new THREE.Vector2(v3[components[0]], v3[components[1]]),
        ]);
      });
      geometry.uvsNeedUpdate = true;
      return geometry;
    },
    createMesh(geom, color) {
      // assign two materials
      //color = "#ff0000";
      var meshMaterial = new THREE.MeshPhongMaterial({
        color: color,
        shininess: 100,
        metal: true,
      });
      var mesh = new THREE.Mesh(geom, meshMaterial);

      mesh.rotation.x = Math.PI;
      return mesh;
    },
    downloadObj() {
      var blob = new Blob([this.objDownloadData], {
        type: "text/plain",
      });
      let strippedFilename = this.svgFilename.substring(
        0,
        this.svgFilename.lastIndexOf(".")
      );
      FileSaver.saveAs(blob, strippedFilename + ".obj");
    },
    startEditor() {
      const coords = {
        x: this.camera.position.x,
        y: this.camera.position.y,
        z: this.camera.position.z,
      };

      const firstCheckpoint = {
        x: 0,
        y: 85,
        z: 70,
      };

      new TWEEN.Tween(coords)
        .to(
          { x: firstCheckpoint.x, y: firstCheckpoint.y, z: firstCheckpoint.z },
          1500
        )
        .easing(TWEEN.Easing.Sinusoidal.InOut)
        .onUpdate(() => {
          this.camera.position.set(coords.x, coords.y, coords.z);

          this.camera.lookAt(0, 0, 0);
        })
        .onComplete(() => {})
        .start()
        .chain(
          new TWEEN.Tween(coords)
            .to({ x: 0, y: 35, z: 0 }, 1000)
            .easing(TWEEN.Easing.Sinusoidal.InOut)
            .onUpdate(() => {
              this.camera.position.set(coords.x, coords.y, coords.z);

              this.camera.lookAt(0, 0, 0);
            })
            .onComplete(() => {
              this.isEditorEnabled = true;
              this.scene.remove(this.svgExtruded);
              this.isObjDownloadReady = false;
            })
        );

      this.animate((time) => {
        this.animateThreeJs();
        TWEEN.update(time);
      });

      this.controls.enabled = false;
    },
    animate(callback) {
      function loop(time) {
        callback(time);
        requestAnimationFrame(loop);
      }
      requestAnimationFrame(loop);
    },
    onPickFile() {
      this.$refs.fileInput.click();
    },
    onFilePicked(event) {
      let svgFile;

      const files = event.target.files;
      let filename = files[0].name;
      console.log(filename);
      this.svgFilename = filename;
      const fileReader = new FileReader();
      fileReader.addEventListener("load", () => {
        svgFile = fileReader.result;
        this.$refs.svgContainer.innerHTML = new DOMParser().parseFromString(
          svgFile,
          "image/svg+xml"
        ).documentElement.innerHTML;

        let BBox = this.$refs.svgContainer.children[0].getBoundingClientRect();
        console.log(BBox);
        if (BBox.width > BBox.height) {
          this.setZoom(BBox.width / this.maxBounding.x);
        } else {
          this.setZoom(BBox.height / this.maxBounding.y);
        }
        //let that = this;
        /* that.svgDraggable = new Draggable(this.$refs.draggable, {
          grid: 1,
          smoothDrag: false,
          limit: function(x, y) {
            let BBox = that.$refs.svgContainer.children[0].getBoundingClientRect();
                        if (
              BBox.width >= this.maxBounding.x ||
              BBox.height >= this.maxBounding.y
            ) {
              return { x: x, y: y };
            }
            let coordinates = {
              x: x,
              y: y,
            };

            if (x < 0) {
              coordinates.x = 0;
            }
            if (y < 0) {
              coordinates.y = 0;
            }

            if (x > that.maxBounding.x - BBox.width) {
              coordinates.x = that.maxBounding;
            }
            if (y > that.maxBounding.x - BBox.height) {
              coordinates.y = that.maxBounding;
            }

            return coordinates;
          },
          onDragEnd: function(ele, x, y) {
            let BBox = that.$refs.svgContainer.getBBox();

            if (isNaN(x)) {
              x = that.maxBounding.x - BBox.width;
            }
            if (isNaN(y)) {
              y = that.maxBounding.y - BBox.height;
            }
            that.svgTranslation.x = x;
            that.svgTranslation.y = y;
          },
        }); */
        //this.loadSVG(data64SVG);
      });
      fileReader.readAsText(files[0]);

      this.isSvgUploaded = true;
    },
    clearSVG() {
      this.$refs.svgContainer.innerHTML = "";
      this.isSvgUploaded = false;
      console.log(this.scene);
    },
    editFinished() {
      this.loadSVG(this.svgToBase64(this.$refs.svgContainer));

      const coords = {
        x: this.camera.position.x,
        y: this.camera.position.y,
        z: this.camera.position.z,
      };
      new TWEEN.Tween(coords)
        .to({ x: 0, y: 150, z: 220 }, 1000)
        .easing(TWEEN.Easing.Sinusoidal.InOut)
        .onUpdate(() => {
          this.camera.position.set(coords.x, coords.y, coords.z);
          this.camera.lookAt(0, 0, 0);
        })
        .onComplete(() => {
          console.log(this.camera.rotation);
        })
        .start();

      this.animate((time) => {
        this.animateThreeJs();
        TWEEN.update(time);
      });
      this.isEditorEnabled = false;
      this.controls.enabled = true;
    },
    svgToBase64(element) {
      let stringElement = new XMLSerializer().serializeToString(element);
      return "data:image/svg+xml;base64," + window.btoa(stringElement);
    },
    randomColor() {
      let color = "#" + Math.floor(Math.random() * 16777215).toString(16);
      return color;
    },
    colorToHex(color) {
      let hexColor = "#";
      let r = color.r * 255;
      let g = color.g * 255;
      let b = color.b * 255;

      hexColor += ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1);
      return hexColor;
    },
    onSVGHover() {
      let rect = this.$refs.svgContainer.children[0].getBoundingClientRect();

      this.$refs.svgBoundingBox.style.width = rect.width + "px";
      this.$refs.svgBoundingBox.style.height = rect.height + "px";
    },
    setZoom(scale) {
      console.log(scale);
      this.svgScale = scale;
      let viewValue = "0 0 " + 510 * scale + " " + 510 * scale;
      this.$refs.svgContainer.setAttribute("viewBox", viewValue);
    },
  },
};
</script>
